/* Homepage styles */
.homepage {
    font-family: "ui-sans-serif", serif;
    display: flex;
    overflow-x: hidden;
}

/* Body styles */


/* Project list styles */
.project-list {
    display: flex;
    padding: 10px;
    width: 85%;
    height: fit-content;
    overflow-x: auto;
    scrollbar-width: thin;
    /*margin-inside: 1rem;*/
    /*scrollbar-color:;*/
    margin-left: 1rem;
    margin-top: 1rem;
    /*padding-left: 1rem;*/
    /*background-color: #29292a;*/
    border-radius: 10px;
}

.title{
    color: white;
    font-family: sans-serif ;
    font-weight: bold;
    font-size: 1.4rem;
    margin-left: 3rem;
    margin-top: 2rem;
}


@media only screen and (max-width: 600px) {
    .project-list{
        /*margin-top: 0rem;*/
        margin-left: 0rem;
        margin-right: 0rem;
        flex-direction: column;
        width: 100%;
        height: 80vh;
        align-items: center;
    }
}

