.profile-bar {
    position: relative;
    height: 5rem; /* Increased height */
    display: flex;
    align-items: center;
    padding: 0 20px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
    font-family: Arial, sans-serif; /* Better font */
    color: white; /* Text color */
    /*overflow: hidden;*/
    z-index: -1/* Ensure texture overlay doesn't overflow */;
    margin-top: 1rem;
    margin-left: 1rem;
    margin-right: 1rem;
    border-radius: 10px;
}

@media only screen and (max-width: 600px) {
    .profile-bar{
        margin-top: 0rem;
        margin-left: 0rem;
        margin-right: 0rem;
    }
}


.username {
    font-size: 1.8rem;
    font-weight: bold;
    margin-left: 1rem;
}


.avatar {
    width: 3rem; /* Adjust width as needed */
    height: auto; /* Adjust height as needed */
    border-radius: 50%;
    object-fit: cover; /* Ensures the image covers the entire space */
}



