.button{
    background-color: #8678e3;
    border: none;
    cursor: pointer;
    padding: 0.5rem;
    border-radius: 2px;
    font-family: ".AppleSystemUIFont", serif;
    color: white;
    /*margin-right: 5rem;*/
    font-weight: bold;
    transition: background-color 0.3s ease;
}

.button:hover{
    background-color: #675abb;
    /*transition-delay: 0.3s;*/
}

.navbar {
    display: flex;
    position: relative;
    flex-direction: column;
    align-items: flex-start;
    background-color: #29292a;
    margin-left: 1rem;
    padding: 2rem;
    /*z-index: 1;*/
    border-radius: 10px;
}


.navbar-mobile{
    display: flex;
    position: absolute;
    flex-direction: column;
    align-items: flex-start;
    top : 0;
    background-color: #29292a;
    height: calc(100% - 4rem);
    width: 70%;
    padding: 2rem;
    z-index: 1;
    border-radius: 10px;
    transition: left 0.5s ease;
    left: -100%;
}

.navbar-mobile.active {
    left: 0; /* Move the navbar to the right side when active */
}

.navbar-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: transparent;
    z-index: 999; /* Ensure the overlay covers everything */
}
